<template>
	<view class="container mms-page wechat-page">
		<!--  -->
		<view class="search-area">
			<view class="search-icon"  @click="showDrawer"><uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-sousuo" size="24"></uni-icons></view>
			<view class="search-info">
				<view class="icon-scan" @click="onScan">
					<uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-saoyisao" size="28"></uni-icons>
				</view>
			</view>
		</view>
		<view class="mms-list">
			<view class="section-title">
				<view class="section-title-text">物料列表</view>
				<view class="title-right-txt">查询结果：{{resListTotal}} 条记录</view>
				<view class="section-title-txt">搜索条件：{{ searchInfo }}</view>
			</view>
			<view class="list" v-if="pageType==1">
				<view class="list-item"  v-for="item in resList" :key="item.materialsNo">
					<view class="list-item-title">
						<view class="list-title-icon">
							<uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-renwumingcheng" size="18"></uni-icons> 
						</view>
					   {{item.materialsName}}
					</view> 
			        <view class="list-item-detail" @click="onOpenDetail(item.materialsNo)"><uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-right" size="24"></uni-icons></view>
					<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view> 
					<view>规格型号：<view class="list-item-info">{{item.materialsModel}}</view></view>
					<view>库存数量：<view class="list-item-info">{{item.inStockNum}} {{item.materialsUnit}}</view></view>
					<!-- <view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view> -->
				</view> 
				<view v-if="showMore"><button class="more-btn" @click="onMore"> 更多 <uni-icons custom-prefix="iconfont" color="##6d3b85" type="icon-icon_duojixiala" size="18"></uni-icons></button></view>
			</view>
			<view class="list" v-else>
				<view class="list-item"  v-for="item in resList" :key="item.materialsNo">
					<view class="list-item-title">
						<view class="list-title-icon"> 
							<uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-renwumingcheng" size="18"></uni-icons> 
						</view>
					   {{item.materialsName}}
					</view>
				    <!-- <view class="list-item-detail" @click="onOpenDetail(item.materialsNo)"><uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-right" size="24"></uni-icons></view> -->
					<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view>
					<view>规格型号：<view class="list-item-info">{{item.materialsModel}}</view></view>
					<view>库存数量：<view class="list-item-info">{{item.locationNum}} {{item.materialsUnit}}</view></view>
					<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
				</view>
				<view v-if="showMore"><button class="more-btn" @click="onMore"> 更多 <uni-icons custom-prefix="iconfont" color="##6d3b85" type="icon-icon_duojixiala" size="18"></uni-icons></button></view>
			</view>
		</view>
		
		<uni-drawer ref="showRight" mode="right" width="260" :mask-click="false">
			<view class="mms-drawer-body">
				<view class="mms-close" @click="closeDrawer" type="primary">x</view>
				
				<view class="mms-drawer-search">
					<radio-group class="mms-drawer-searchtype" v-model="searchType" @change="onSearchType">
						<label class="searchtype-radio"><radio value="1" checked="true">按物料</radio></label> 
						<label class="searchtype-radio"><radio value="2">按货架</radio></label>
					</radio-group>
					<uni-forms :modelValue="searchData">
					        <uni-forms-item v-if="searchType==1" name="hobby" label="物料名称">
					            <uni-easyinput type="text" v-model="searchData.materialsName" placeholder="物料名称" />
					        </uni-forms-item>
					        <uni-forms-item v-if="searchType==1" name="hobby" label="物料编号">
					            <uni-easyinput type="text" v-model="searchData.materialsNo" placeholder="物料编号" />
					        </uni-forms-item>
					        <uni-forms-item v-if="searchType==1" name="hobby" label="规格型号">
					            <uni-easyinput type="text" v-model="searchData.materialsModel" placeholder="规格型号" />
					        </uni-forms-item>
							<uni-forms-item v-else name="hobby" label="存放位置">
							   <!-- <uni-easyinput type="text" v-model="searchData.storehouseName" placeholder="规格型号" /> -->
							   <uni-data-picker :localdata="materialsAddrList" popup-title="请选位置信息" @change="onchange"></uni-data-picker>
							</uni-forms-item>
					</uni-forms>
					<button class="mms-btn" @click="getProInfo">查 询</button>
				</view>
			</view>
		</uni-drawer>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js');
	export default {
		data() {
			return {
				userState: {},
				searchData:{}, 
				searchType:1,
				pageType:1,
				searchInfo:'', // 搜索条件文本
			    resList:[],
				resListAll:[],
				resListTotal:0,
				interval: 20, // 每页显示条数
				pageNum: 20, // 当前显示条数
				showMore:false,
				materialsAddrList:[],
				materialsAddrLength:0,
				storehouse:{}
			}
		},
		onLoad(option) {
			if(option.materialsNo){
				this.searchData = {};
				this.searchData.materialsNo = option.materialsNo;
				this.getProInfo(); 
			}
		},
		created(){
			this.userState = util.xcStorage.getStorageSync('userState');
			this.getStoreHouseList();
		},
		methods: { 
			// 根据物料编号获取物料信息
			onScan: function() {
				// 只允许通过相机扫码
				var _this = this;
				uni.scanCode({
				    onlyFromCamera: true,
				    success: function (res) { 
						let materialsNo = util.xcUrlCode.getMaterialsNo(res.result, 'code');
						_this.searchData = {};
						_this.searchData.materialsNo = materialsNo;
						
						_this.searchType=1;
						_this.getProInfo();
				    }
				});
			},
			onSearchType(e){
				this.pageNum =20;
				this.searchType = e.detail.value;
			}, 
			// 根据物料编号获取物料信息
			getProInfo(){
				var _this = this;
				this.searchInfo = "";
				let url = '';
				_this.pageType = _this.searchType;
				if(_this.searchType==1){
					if(_this.searchData.materialsName){
						this.searchInfo = this.searchInfo + " " + _this.searchData.materialsName;
					}
					if(_this.searchData.materialsNo){
						this.searchInfo = this.searchInfo + " " + _this.searchData.materialsNo;
					}
					if(_this.searchData.materialsModel){
						this.searchInfo = this.searchInfo + " " + _this.searchData.materialsModel;
					}
					if(this.searchData.storehouseId){
						delete this.searchData.storehouseId;
					}
					// this.searchData.shelfNumber = 5;
					// this.searchData.shelfLayerNumber = 2;
					
					url = util.xcRequestURL.WAREHOUSE.getMaterialList + "?token=" + _this.userState.token;
					util.xcRequest.xcGet(url,this.searchData, function(res) {
						// const { result } = res;
						if(res.status == 200){
							let resList = res.result;
							if(resList.length > 0){ 
							    // _this.resList = res.result;
								_this.resListAll = res.result;
								_this.resListTotal = resList.length;
								_this.$refs.showRight.close();
								_this.getListShow();
							}else{
								_this.resList = [];
								_this.resListTotal = 0;
								uni.showToast({
									title:'暂无记录！',
									duration:3000 
								});
							}
						}
					});
				}else{
					this.searchData = {};
					if(_this.storehouse.name){
						this.searchData.storehouseId = this.storehouse.id;
						_this.searchInfo = _this.searchInfo + " " + _this.storehouse.name;
						url = util.xcRequestURL.WAREHOUSE.getXcMaterialsBy + "?token=" + _this.userState.token+'&storehouseId='+this.searchData.storehouseId;
					}else{
						url = util.xcRequestURL.WAREHOUSE.getXcMaterialsBy + "?token=" + _this.userState.token;
					}
					 
					 util.xcRequest.xcPost(url,{}, function(res) {
					 	// const { result } = res;
					 	if(res.status == 200){
					 		let resList = res.result;
					 		if(resList.length > 0){ 
					 		    // _this.resList = res.result;
					 			_this.resListAll = res.result;
					 			_this.resListTotal = resList.length;
					 			_this.$refs.showRight.close();
					 			_this.getListShow();
					 		}else{
								_this.resList = [];
								_this.resListTotal = 0;
								uni.showToast({
									title:'暂无记录！',
									duration:3000 
								});
							}
					 	}
					 });
				}
			},
			showDrawer() {
			    this.$refs.showRight.open();
			},
			closeDrawer() {
			    this.$refs.showRight.close();
			},
			onOpenDetail(code){
				// console.log(code);
				let pageUrl='/pages/materialsDetail/materialsDetail?code='+code;
				uni.navigateTo({
				  url: pageUrl,
				});
			},
			// 更多操作 
			onMore: function(e){
			  this.getListShow();
			},
			getListShow(){
				const l1 = this.resListTotal;
				if (this.pageNum < l1) {
				  this.resList = this.resListAll.slice(0, this.pageNum);
				  this.pageNum = this.pageNum + this.interval;
				  this.showMore = true;
				} else {
				  this.resList = this.resListAll.slice(0, l1);
				  this.pageNum = l1;
				  this.showMore = false;
				}
			},
			// 获取物料信息位置信息列表
			getStoreHouseList(){
				var _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getStoreHouseList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{}, function(res) {
					if(res.status == 200){
						let materialsAddrList = res.result;
						if(materialsAddrList.length > 0){
							for(let i=0;i<materialsAddrList.length;i++){
								materialsAddrList[i].text = materialsAddrList[i].storehouseName;
								materialsAddrList[i].value = materialsAddrList[i].id;
							}
							_this.materialsAddrList = materialsAddrList;
							//_this.materialsInfo.storehouseName1 = materialsAddrList[0].storehouseName;
							// _this.materialsInfo.storehouseId1 = materialsAddrList[0].id;
						}
					}
				});
			},
			// 位置监听
			onchange(e) {
				// console.log(e.detail.value[0].text)
			    const data = e.detail.value;
				// console.log(data[0].text);
				this.storehouse.id = data[0].value;
				this.storehouse.name = data[0].text;
				// this.materialsInfo.changeStockNum = 
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import "../../static/css/style.css"; 
	
</style>
